@tailwind base;
@tailwind components;
@tailwind utilities;


.styleLogo {
    @apply rounded-full h-8 w-8 hover:cursor-pointer hover:border hover:border-yellow-500;
  }

  .cards{
    @apply flex md:flex-row flex-col flex-wrap gap-10 justify-center mx-auto w-10/12 content-evenly mt-2  text-quatro;
  }

  .card{
   @apply flex flex-col bg-secondary border-opacity-50 shadow-sm w-full  shadow-white rounded-2xl overflow-hidden;
  }

  .cardDescription{
    @apply  text-start text-xl py-2 flex items-center shadow-white shadow-sm  justify-between flex-wrap px-6;
  }
  .imgCard{
    @apply h-64 w-full overflow-hidden border-x-primary shadow-white shadow-sm;
  }

  .buttonCard{
    @apply w-full border-t-2 bg-quatro text-white hover:bg-white hover:text-quatro hover:uppercase py-2;
  }
  .imgLangages{
    @apply flex gap-2;
  }
