.centrer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.border {
  border: 0.2rem double rgba(0,0,0,0.5);
  background:whitesmoke;
}

.slideInOut {
  animation: slideInOut 8s ease infinite;
}

@keyframes slideInOut {
    0% {
      transform: translateX(0);
    }
    50% {
      transform: translateX(500%);
    }
    100% {
      transform: translateX(0);
    }
  }
  
